.card {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  padding: 32px 16px 16px;
  border: 1px solid #f3f3f3;
  border-radius: 5px;

  &__img {
    width: 160px;
    height: 134px;
    margin-bottom: 40px;
  }

  &__title {
    @extend %font-500;
    margin-bottom: 4px;
  }

  &__code {
    @extend %font-400;

    width: 100%;

    margin-bottom: 16px;
  }

  &__rating {
    display: flex;
    justify-content: space-between;
    width: 100%;

    margin-bottom: 24px;
  }

  &__feedback {
    @extend %font-400;

    align-self: center;
    color: #060b35;
  }

  &__price {
    display: flex;
    justify-content: space-between;
    width: 100%;

    margin-bottom: 16px;

    &-hint {
      @extend %font-400;

      font-size: 12px;
      line-height: 18px;
    }

    &-value {
      @extend%font-700;
    }
  }

  &__buy-button {
    display: flex;
    justify-content: center;
    align-items: center;

    width: 166px;
    height: 40px;

    text-decoration: none;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 14px;
    line-height: 16px;
    color: #fff;

    background: #00acdc;
    border-radius: 5px;

    &:hover {
      color: #00acdc;
      border-color: #00acdc;
      background: #fff;
    }
  }
}
